.skillSec {
    padding: 10px 20px 20px;
    box-shadow: 0px 0px 30px 1px rgba(0, 0, 0 ,0.20);
    border-radius: 3px;
    margin-bottom: 50px;
    .skillBox{
        ul {
            display: flex;
            flex-flow: wrap;
            justify-content: space-around;
            list-style:none;
            li {
                margin: 10px 10px;
                white-space: nowrap;
                padding: 10px 20px;
                color: black;
                border: 2px solid #ff5e00;
                // color: white;
                // background: rgb(255, 94, 0);
                // box-shadow: inset 0px 0px 40px 1px #a757ff;
                border-radius: 3px;
            }
        }
    }
}

@media only screen and (max-width:480px) {
    .skillSec {
        .skillBox  {
            ul {
                justify-content: right;
            }
        }
    }
}