.recentwork {
    padding: 10px 20px 20px;
    box-shadow: 0px 0px 30px 1px rgba(0, 0, 0 ,0.20);
    border-radius: 3px;
    margin-bottom: 50px;

    ul {
        display: flex;
        justify-content: space-around;
        list-style: none;
        flex-flow: wrap;
        padding: 0px;
        li {
            display: flex;
            margin: 10px;
            a {
                position: relative;
                color: #fff;
                padding: 30px;
                border-radius: 3px;
                // color: black;
                // border: 2px solid #ff5e00;
                background: rgb(255, 94, 0);
                // box-shadow: inset  0px 0px 40px 1px #972100, 4px 4px 5px 0px #c4c4c4;
                text-decoration: none;
                &::after {
                    padding-top: 40px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    height: calc(100% - 40px);
                    width: 100%;
                    top: 0%;
                    left: 0;
                    background: rgba(0, 0, 0, 0.514);
                    content: "view";
                    overflow: hidden;
                    visibility: hidden;
                    opacity:0;
                    transition: all 0.3s ease-in-out;
                }
                &:hover {
                    &::after {
                        visibility: visible;
                        opacity:1;
                        transition: all 0.3s ease-in-out;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 900px) {
    .recentwork {
        ul {
            li {
                width: 30%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
        
                a {
                    text-overflow: ellipsis;
                    width: 100%;
                    overflow: hidden;
                    text-align: center;
                }
        }
        }
    }
}

@media only screen and (max-width: 480px) {
    .recentwork {
        ul {
            li {
                width: 100%;

                a {
                    padding: 15px;
                }
        }
        }
    }
}