.pageContent {
    padding: 50px;
    z-index: 99;
    position: relative;
}

canvas {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    z-index: 9;
}

@media only screen and (max-width:480px) {
    .pageContent {
        padding: 50px 20px;
    }
}