.banner {
    position: relative;
    color: #fff;
    background: linear-gradient(to right, #fe8c00, #f83600);
    background-attachment: fixed;
    padding: 60px 0px ;

    &:after {
        content: " ";
        right: 0;
        bottom: 0;
        opacity: 1;
        left: 0;
        height: 110px;
        position: absolute;
        z-index: 0;
        background-position: center top;
        background-position-x: 50%;
        background-size: 150%;
        background-repeat: no-repeat;
        background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0naHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmcnIHZpZXdCb3g9JzAgMCAxNjc5IDE5OC42Jz48cGF0aCBkPSdNODM5LjUgOTYuN0M0ODUuMyA5Ni43IDE3NC41IDU3LjkgMCAwdjE5OC42aDE2NzlWMGMtMTc0LjUgCTU3LjktNDg1LjMgOTYuNy04MzkuNSA5Ni43eicgZmlsbD0nI2ZmZicvPjwvc3ZnPg==);
    }

    .content {
        display: flex;
        justify-content: space-around;
        align-items: center;
        position: relative;
        padding: 0px 50px;
        flex-flow: wrap;
        
        &::after {
            content: " ";
            right: 0;
            bottom: -30%;
            opacity: 0.1;
            left: 0;
            height: 100%;
            width: 100%;
            position: absolute;
            z-index: 0;
            background-position: center;
            background-size: cover;
            background-image: url(data:image/svg+xml;base64,);
        }
        .profilePic  {
            flex: 1;
            img {
                max-width: 100%;
                width: 350px;
            }
        }
        .decription {
            flex:1;
            padding: 0px 50px;
            text-align: center;

            .designation {
                font-size: 2rem;
            }
        }
    }
}

@media only screen and (max-width: 900px) {
    .banner {
        .content {
            padding: 0px 20px;
            .profilePic  {
                flex: 4;
            }
            .decription {
                padding: 0px 20px;
                flex: 3;
            }
        }
    }
}

@media only screen and (max-width: 480px) {
    .banner {
        &::after {
            width: 100%;
            bottom: -6%;
        }
        .content {
            .profilePic  {
                width: 80%;
                flex:unset;
            }
            .decription {
                width: 90%;
                flex:unset;
            }
        }
    }
}